import {Component, OnInit} from '@angular/core';
import {AppHelpers} from "app/shared/resources/app-helpers";
import {FormGroup, Validators} from "@angular/forms";

@Component({
  selector: 'app-course-index',
  templateUrl: './course-index.component.html',
  styleUrls: ['./course-index.component.scss']
})
export class CourseIndexComponent extends AppHelpers implements OnInit {
  form: FormGroup;

  constructor() {
    super();
    console.log(this.globalFunctions)
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      name2: [, Validators.required,],
      name: [, Validators.required,],
      bankId: [, Validators.required,],
    })
  }

  get formCtrls() {
    return this.form.controls
  }

  clearForm() {
    this.form.reset()
  }

}
