import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ICourseAttachmentModel,
  ICourseAttachmentRequest,
} from "app/training/models/course/attachment";

@Injectable({
  providedIn: "root",
})
export class CourseAttachmentService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ICourseAttachmentRequest) {
    return this.post<IResponse<ICourseAttachmentModel>>(
      "Training/Course/Create",
      data
    );
  }

  getAllByCourseId(courseId: number) {
    return this.get<IResponse<ICourseAttachmentModel[]>>(
      "Training/Course/GetAll",
      {
        params: {
          $filter: `courseId eq ${courseId}`,
        },
      }
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("Training/Course/Delete", {
      params: { id: id },
    });
  }
}
