import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ICourseOutlineModel,
  ICourseOutlineRequest,
} from "app/training/models/course/outline";

@Injectable({
  providedIn: "root",
})
export class CourseOutlineService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ICourseOutlineRequest) {
    return this.post<IResponse<ICourseOutlineModel>>(
      "Training/CourseOutline/Create",
      data
    );
  }

  getAllByCourseId(courseId: number) {
    return this.get<IResponse<ICourseOutlineModel[]>>(
      "Training/CourseOutline/GetAll",
      {
        params: {
          $filter: `courseID eq ${courseId}`,
        },
      }
    );
  }
}
