import { Component, Input, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import {
  ICourseOutlineModel,
  ICourseOutlineRequest,
} from "app/training/models/course/outline";
import { CourseOutlineService } from "app/training/services/course/course-outline.service";

@Component({
  selector: "app-course-outline",
  templateUrl: "./course-outline.component.html",
  styleUrls: ["./course-outline.component.scss"],
})
export class CourseOutlineComponent extends AppHelpers implements OnInit {
  @Input() courseId: number;

  form: FormGroup;
  submitted: boolean = false;

  constructor(private courseOutlineService: CourseOutlineService) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      // id: [0, { nonNullable: true }],
      outline: [null, [Validators.required]],
      outlinedesc: [null, [Validators.required]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.courseId > 0) this.getAll();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.courseOutlineService
      .getAllByCourseId(this.courseId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ICourseOutlineModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.form.patchValue(res.data);
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ICourseOutlineRequest = {
        outline: this.form.value.outline,
        outlinedesc: this.form.value.outlinedesc,
        courseID: this.courseId,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: ICourseOutlineRequest) {
    this.showLoading();
    this.courseOutlineService
      .create(model)
      .subscribe((res: IResponse<ICourseOutlineModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }
}
